
.heder-site{
    position: relative;
    padding-left: 25px;
    padding-right: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.heder-site .conteiner-burger-menu {
    width: 30px;
    height: 30px;
}
.heder-site .conteiner-burger-menu img{
    width: 100%;
    height: 100%;

}
.heder-site .hederLogoImg{
    width: 30%;
}
.search-bar{
    width: 30px;
    height: 30px;
}
.search-bar img{
    width: 100%;
    height: 100%;
}
.m-t20{
    margin-top: 20px;
}
.bottom-icon-marvel{
    width: 70px;
    height: 70px;
    position: fixed;
    bottom: 0%;
    z-index: 10;
    left: 50%;
    transform: translate(-50%,20%);
    border: 1px solid #CDCDCD;
    border-radius: 50%;
    background-color: #D50000;
    display: flex;
    justify-content: center;
    align-items: center;
}
.bottom-icon-marvel span{
    transform: translateY(-10%);
    font-size: 50px;
    font-weight: 600;
    color: white;
    font-family: 'Barlow', sans-serif;
}
.menu-burger{
    position: fixed;
    top: 0%;
    z-index: 15;
    width: 100vw;
    height: 100vh;
    background-color: #EB1F29;
    transition: 1s;
}
.menu-burger.menu-burger .x-icon{
    width: 40px;
    height: 40px;
    padding-left: 25px;
}
.menu-burger.menu-burger .x-icon img{
    width: 100%;
    height: 100%;

}
.user-conteyner{
    padding: 15px;
    display: grid;
    justify-content: center;
    align-items: center;
}
.user-conteyner img{
    width: 92px;
    height: 92px;
    border: 2px solid #DEA000;
    border-radius: 50%;
}
.active-menu{
    background-color: rgba(0,0,0,0);
    position: relative;
    width: 200px;
    height: 45px;
    margin: auto;
    margin-top: 20px;
    display: grid;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
}
.active-menu span{
    width: 70%;
    margin: auto;
}
.active-menu img{
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 19px;
    height: 23px;
}
.MarvelFon2{
    position: absolute;
    bottom: 10%;
    left: 50%;
    width: 80%;
    transform: translateX(-50%);
}
.title-text{
    margin: 10px 0;
}
.title-text span{
    font-size: 36px;
    font-weight: 500;
    color: white;
    font-family: 'Barlow', sans-serif;
}
.title-text p{
    font-size: 18px;
    letter-spacing: 1.8px;
    color: rgba(255, 255, 255, 0.48);
    font-family: 'Barlow', sans-serif;
}
.razdely{
    margin-top: 30px;
    width: 80%;
    padding: 0 10%;
}
.razdely ul {
    font-size: 14px;
    display: flex;
    color: white;
    justify-content: space-evenly;
    list-style-type: none;
    text-transform: uppercase;
}
.razdely ul li{
    cursor: pointer;
}
.li{
    border-bottom: 1px solid white;
}
.conteiner-content-charectors{
    margin-top: 15px;
    width: 80%;
    padding: 0 10%;
    height: auto;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.search-bar-cont{
    position: fixed;
    top: 0%;
    z-index: 15;
    width: 100vw;
    height: 100vh;
    background-color: #EB1F29;
    transition: 1s;
}
.x-icon-search{
    display: flex;
    justify-content: flex-end;
}
.x-icon-search img{
    width: 40px;
    height: 40px;
    margin-top: 20px;
    margin-right: 20px;
}
.inp-search{
    border: 2px solid white;
    outline: none;
    width: 70%;
    text-align: center;
    font-size: 18px;
    height: 30px;
    border-radius: 5px;
    padding: 2px 15px;
    background-color: rgba(0, 0, 0, 0.37);
    font-family: 'Barlow', sans-serif;
    color: white;
}
::-webkit-input-placeholder { /* WebKit browsers */
    color: white;
}
.inp-search:active{
    border: 1px solid white;

}
.conteiner-search-char{
    width: 80%;
    height: 50%;
    background-color: rgba(0, 0, 0, 0.37);
    margin: auto;
    margin-top: 20px;
    border-radius: 8px;
    border: 1px solid white;
    overflow: auto;
    padding-bottom: 10px;
    }
.spisok-search li{
    list-style-type: none;
    background-color: rgba(255,255,255,0.37);
    margin-top: 8px;
    padding: 5px 10px;
    font-size: 20px;
    color: white;
    margin-right: 10px;
    margin-left: 10px;
}