.random-conteiner-char{
    width: 90%;
    height: auto;
    padding-bottom:10px;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.49);
    margin-top: 20px;
    border: 1px solid #876464;
    border-radius: 8px;
    position: relative;
    margin-bottom: 20px;
}

.random-conteiner-char .loadingImg{
    margin-top: 15px;
    width: 50%;
    animation: loading 1.5s linear infinite;
    border-radius: 50%;
}
@keyframes loading {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
.random-conteiner-char .cont-random-char-info {
    padding: 15px;
}
.random-conteiner-char .cont-random-char-info img{
    width: 55%;
    height: 225px;
    margin-right: 15px;
    border-radius: 5px;
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.39);
}
.random-conteiner-char .cont-random-char-info h2{
    color: rgba(255, 255, 255, 0.9);
    font-family: 'Barlow', sans-serif;
}
.random-conteiner-char .cont-random-char-info span{
    color: rgba(255, 255, 255, 0.7);
    font-size: 12px;
    font-family: 'Barlow', sans-serif;
}
.random-conteiner-char .cont-random-char-info p{
    margin-top: 5px;
    color: rgba(255, 255, 255, 0.7);
    font-size: 16px;
    font-weight: normal;
    height: 140px;
    overflow-Y: auto;
}

.random-comics-char{
    margin: auto;
    width: 90%;
    height: 45px;
    border: 1px solid white;
    border-radius: 5px;
    display: grid;
    justify-content: flex-start;
    align-items: center;
    padding: 5px 5px 5px 10px;
    color: white;
    font-family: 'Barlow', sans-serif;
    color: rgba(255, 255, 255, 0.85);
    margin-bottom: 10px;
}
.btn-random{
    margin: auto;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 70px;
}

.loadingImg-icons{
    animation: loading 0.5s linear infinite;
}
.btn-random img{
    width: 100%;
}