.conteiner-comics{
    margin: auto;
    margin-top: 15px;
    width: 90%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.comics-card{
    width: 150px;
    height: 250px;
    border: 1px solid white;
    position: relative;
    z-index: 2;
    margin: 20px 10px;
}
.comics-card .img-comics-card{
    opacity: 0.7;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.comics-card span{
    display: grid;
    text-align: center;
    color: white;
    background-color: rgba(0, 0, 0, 0.41);
    padding: 10px 0;
    transition: 1s;
}
.comics-card:hover span{
    opacity: 0;
}
.comics-card p{
    position: absolute;
    width: 100%;
    bottom: 0px;
    color: rgba(0,0,0,0);
    height: 100%;
    overflow-y: auto;
    transition: 1s;
}
.comics-card:hover p{
    overflow-y: auto;
    color: white;
    background-color: rgba(0, 0, 0, 0.41);
}
.btn-cont-comics{
    position: absolute;
    z-index: 10;
    bottom: -10%;
    width: 100%;
    height: auto;
    background-color: #DEA000;
    left: 50%;
    transform: translatex(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 0 2px 0 0;
}
