
.conteiner-card-series{
    width: 90%;
    margin: auto;
    background-color: #4c0000;
    color: #CDCDCD;
    height: auto;
    margin-top: 15px;
    padding: 15px 10px;
    border-radius: 8px;
    transition: 1s;
}
.conteiner-card-series img{
    width: 200px;
    height: 200px;
    border-radius: 5px;
}
.conteiner-card-series p{
    display: none;
    transition: 1s;
}
.conteiner-card-series:hover p{
    display: block;
}
.conteiner-card-series span{
    font-size: 12px;
    color: rgba(205, 205, 205, 0.71);
}
.conteiner-card-series button{
    position: absolute;
    bottom: 15px;
    right: 10px;
    width: auto;
    height: auto;
    padding: 5px 7px;
    font-size: 12px;
    font-weight: 700;
    color: black;
    background-color: #DEA000;
    border-radius: 5px;
    border: none;
}