*{
    padding: 0px;
    margin: 0px;
}
body{
    background-color: #4c0000;
}
a{
    text-decoration: none;
}
.imgFon{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    height: 100%;
    z-index: -5;
}
.content{
    transition: 3s;
}
.App .MarvelWelcom{
    color: white;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    box-shadow: 0 0 15px #211d1d, inset 0 0 1px black;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 50px;
    font-size: 30px;
    font-weight: 800;
    animation: marvelWelcom 2s;
}

@keyframes marvelWelcom{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.info-in-error{
    position: fixed;
    top: 70%;
    left: 50%;
    width: 80%;
    transform: translate(-50%,-50%);
    z-index: 15;
}
.info-in-error h2{
    text-align: center;
    color: #c00007;
    text-shadow: 2px 2px 10px black;
}
.info-in-error p{
    font-size: 20px;
    text-align: center;
    color: #c5c0c6;
    text-shadow: 2px 2px 10px black;
}
.info-in-error span{
    text-align: center;
    position: absolute;
    left: 50%;
    top: -50%;
    transform: translate(-50%,-50%);
    animation: textError 1.7s infinite;
    animation-delay: 2s;
    animation-direction: alternate;
    opacity: 0;
}
@keyframes textError {
    0%{
        opacity: 1;
        color: rgba(0,0,0,0.26);
    }
    100%{
        opacity: 1;
        color: white;
    }
}